<!--
 * @Author: syf
 * @LastEditTime: 2022-02-12 23:05:36
 * @Detail: 左右布局模块通用模板
-->
<template>
  <div class="left-right-layout">
    <div :style="{ width: leftWidth }">
      <slot name="leftLayout"></slot>
    </div>
    <div :style="{ width: `100% - ${leftWidth}` }">
      <slot name="rightLayout"></slot>
    </div>
  </div>
</template>
<script>
export default {
  name: 'LeftRightLayout',
  props: {
    leftWidth: {
      type: String,
      default: '15%'
    }
  }
}
</script>
<style lang="less" scoped>
.left-right-layout {
  display: flex;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  >div{
    height: 100%;
    overflow: auto;
    &:first-of-type {
      border-right: 2px solid #eee;
    }
  }
}
</style>